<template>
  <div class="profile">
    <h1>Profile Page</h1>
    <p>Welcome to your profile, Enter/Update your info below:</p>

    <!-- Card with student fields -->
    <CardComponent :title="titleText" :width="600">
      <div class="form-fields">
        <!-- Date Picker for Date of Birth -->
        <DatePickerComponent
          v-model="selectedStudent.dateOfBirth"
          label="Date of Birth"
          :width="200"
          :height="35"
          :fontSize="12"
          color="#0bbb98"
          id="dob"
          placeholder="YYYY-MM-DD"
        />

        <TextBoxComponent
          v-model="selectedStudent.firstName"
          label="First Name"
          size="small"
          :width="200"
          placeholder="Enter first name"
        />
        <TextBoxComponent
          v-model="selectedStudent.lastName"
          label="Last Name"
          size="small"
          :width="200"
          placeholder="Enter last name"
        />
        <TextBoxComponent
          v-model="selectedStudent.parentContactInfo"
          label="Parent Contact Info"
          size="small"
          :width="300"
          placeholder="Enter parent contact info"
        />
        <SelectComponent
          v-model="selectedStudent.grade"
          :options="gradeOptions"
          label="Grade"
          :width="150"
        />
      </div>
      <button @click="saveStudent">Save</button>
    </CardComponent>

    <!-- Student Table -->
    <TableComponent
      :headers="headers"
      :data="students"
      :border="true"
      :borderRadius="10"
      :showActions="true"
      @add="handleAddStudent"
      @delete="confirmDelete"
      @edit="editStudent"
    />

    <!-- Delete Confirmation Modal -->
    <ModalComponent
      v-model:visible="showDeleteModal"
      title="Confirm Deletion"
      width="400px"
      height="200px"
    >
      <p v-if="!deleteError">
        Are you sure you want to delete
        <br />
        <strong>
          {{ studentToDelete?.firstName }} {{ studentToDelete?.lastName }}
        </strong>
        ?
      </p>
      <p v-else style="color: red; font-weight: bold">
        ERROR: {{ deleteError }}
        <br />
        <strong>
          {{ studentToDelete?.firstName }} {{ studentToDelete?.lastName }}
        </strong>
      </p>

      <template #footer>
        <button @click="showDeleteModal = false">Cancel</button>
        <button
          @click="deleteStudentConfirmed"
          :disabled="isDeleting"
          class="button"
        >
          {{ isDeleting ? 'Deleting...' : 'Delete' }}
        </button>
      </template>
    </ModalComponent>

    <div class="details">
      <h3>User Details</h3>
    </div>

    <!-- Add/Edit Student Modal -->
    <ModalComponent
      v-model:visible="showSaveModal"
      title="Save Student"
      width="80%"
      height="80%"
    >
      <div class="form-fields">
        <DatePickerComponent
          v-model="selectedStudent.dateOfBirth"
          label="Date of Birth"
          :width="200"
          :height="35"
          :fontSize="12"
          color="#0bbb98"
          id="dob"
          placeholder="YYYY-MM-DD"
        />
        <TextBoxComponent
          v-model="selectedStudent.firstName"
          label="First Name"
          size="small"
          :width="200"
          placeholder="Enter first name"
        />
        <TextBoxComponent
          v-model="selectedStudent.lastName"
          label="Last Name"
          size="small"
          :width="200"
          placeholder="Enter last name"
        />
        <TextBoxComponent
          v-model="selectedStudent.parentContactInfo"
          label="Parent Contact Info"
          size="small"
          :width="300"
          placeholder="Enter parent contact info"
        />
        <SelectComponent
          v-model="selectedStudent.grade"
          :options="gradeOptions"
          label="Grade"
          :width="150"
        />
      </div>

      <template #footer>
        <BtnComponent
          label="Cancel"
          type="secondary"
          @click="showSaveModal = false"
        />
        <BtnComponent
          label="Save"
          type="primary"
          :hoverColor="'#099f80'"
          :disabled="isDeleting"
          @click="saveStudent"
        />
      </template>
    </ModalComponent>
  </div>
</template>

<script>
  import dateMixin from '@/mixins/dateMixin'
  import BtnComponent from '../BtnComponent.vue'
  import CardComponent from '../Card.vue'
  import DatePickerComponent from '../DatePickerComponent.vue'
  import ModalComponent from '../ModalComponent.vue'
  import SelectComponent from '../SelectComponent.vue'
  import TableComponent from '../TableComponent.vue'
  import TextBoxComponent from '../TextBoxComponent.vue'

  export default {
    name: 'Profile',
    components: {
      CardComponent,
      TableComponent,
      TextBoxComponent,
      DatePickerComponent,
      SelectComponent,
      ModalComponent,
      BtnComponent,
    },
    mixins: [dateMixin],
    data() {
      return {
        titleText: 'Profile',
        headers: [
          { label: 'ID', field: 'id', width: 60, align: 'center' },
          { label: 'Date of Birth', field: 'dateOfBirth', width: 120 },
          { label: 'First Name', field: 'firstName', width: 120 },
          { label: 'Last Name', field: 'lastName', width: 120 },
          {
            label: 'Parent Contact Info',
            field: 'parentContactInfo',
            width: 200,
          },
          { label: 'Grade', field: 'grade', width: 80 },
        ],
        students: [],
        selectedStudent: {
          id: null,
          dateOfBirth: '',
          firstName: '',
          lastName: '',
          parentContactInfo: '',
          grade: '',
        },
        gradeOptions: [
          { label: 'Kindergarten', value: 'K' },
          { label: 'First Grade', value: '1' },
          { label: 'Second Grade', value: '2' },
          { label: 'Third Grade', value: '3' },
          { label: 'Fourth Grade', value: '4' },
          { label: 'Fifth Grade', value: '5' },
          { label: 'Sixth Grade', value: '6' },
        ],
        showDeleteModal: false,
        studentToDelete: null,
        deleteError: '',
        isDeleting: false,
        showSaveModal: false,
      }
    },
    mounted() {
      this.fetchStudents()
    },
    methods: {
      // Populate modal with selected student for editing
      editStudent(student) {
        this.selectedStudent = { ...student }
        this.showSaveModal = true
      },

      // Show empty modal for adding new student
      handleAddStudent() {
        this.selectedStudent = {
          id: null,
          dateOfBirth: '',
          firstName: '',
          lastName: '',
          parentContactInfo: '',
          grade: '',
        }
        this.showSaveModal = true
      },

      // Open delete confirmation modal
      confirmDelete(student) {
        this.studentToDelete = student
        this.deleteError = ''
        this.showDeleteModal = true
      },

      // Delete student API
      async deleteStudentConfirmed() {
        if (!this.studentToDelete) return
        this.isDeleting = true
        const token = localStorage.getItem('authToken')

        try {
          const response = await fetch(
            `http://localhost:8080/api/students/delete/${this.studentToDelete.id}`,
            {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`,
              },
            },
          )

          if (!response.ok) {
            const text = await response.text()
            throw new Error(text || 'Failed to delete student')
          }

          this.students = this.students.filter(
            (s) => s.id !== this.studentToDelete.id,
          )
          this.studentToDelete = null
          this.showDeleteModal = false
        } catch (err) {
          this.deleteError = err.message || 'An unknown error occurred'
        } finally {
          this.isDeleting = false
        }
      },

      // Fetch all students
      async fetchStudents() {
        try {
          const token = localStorage.getItem('authToken')
          const response = await fetch(
            'http://localhost:8080/api/students/getStudents',
            {
              method: 'GET',
              headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`,
              },
            },
          )

          if (!response.ok) {
            const text = await response.text()
            throw new Error(`HTTP ${response.status}: ${text}`)
          }

          const data = await response.json()
          this.students = data.map((student) => ({
            id: student.studentId,
            dateOfBirth: student.dateOfBirth,
            firstName: student.firstName,
            lastName: student.lastName,
            parentContactInfo: student.parentContactInfo,
            grade: student.grade,
          }))
        } catch (err) {
          console.error('Error fetching students:', err)
        }
      },

      // Save or update student
      async saveStudent() {
        try {
          const token = localStorage.getItem('authToken')
          if (!token) {
            throw new Error('No auth token found. Please login first.')
          }

          const isEdit = !!this.selectedStudent.id
          const url = isEdit
            ? `http://localhost:8080/api/students/update/${this.selectedStudent.id}`
            : 'http://localhost:8080/api/students/save'
          const method = isEdit ? 'PUT' : 'POST'

          const studentToSend = {
            ...this.selectedStudent,
            dateOfBirth: this.toISODate(this.selectedStudent.dateOfBirth),
          }

          const response = await fetch(url, {
            method,
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
            body: JSON.stringify(studentToSend),
          })

          if (!response.ok) {
            const text = await response.text()
            throw new Error(`HTTP ${response.status}: ${text}`)
          }

          const savedStudent = await response.json()

          if (isEdit) {
            const index = this.students.findIndex(
              (s) => s.id === savedStudent.id,
            )
            if (index !== -1) this.students[index] = savedStudent
          } else {
            this.students.push(savedStudent)
          }

          this.selectedStudent = {
            id: null,
            dateOfBirth: '',
            firstName: '',
            lastName: '',
            parentContactInfo: '',
            grade: '',
          }
          this.showSaveModal = false
        } catch (err) {
          console.error('Error saving student:', err)
          alert(`Failed to save student: ${err.message}`)
        }
      },
    },
  }
</script>

<style scoped>
  .profile {
    padding: 2rem;
  }

  /* Form fields inside card */
  .form-fields {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1rem;
  }

  /* Save button styling */
  button {
    padding: 6px 12px;
    border: none;
    background: #0bbb98;
    color: white;
    font-weight: bold;
    border-radius: 6px;
    cursor: pointer;
  }

  button:hover {
    background: #099f80;
  }

  /* Modal footer buttons */
  .modal-footer button {
    margin-left: 10px;
    padding: 6px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .modal-footer button:first-child {
    background-color: #ccc;
  }
  .modal-footer button:last-child {
    background-color: #dc3545;
    color: white;
  }
</style>
