<template>
  <div class="table-wrapper" :style="tableStyles">
    <table :class="['custom-table', { 'table-border': border }]">
      <!-- Header -->
      <thead>
        <tr>
          <th
            v-for="(col, index) in headers"
            :key="index"
            :style="{
              width: col.width ? col.width + 'px' : 'auto',
              textAlign: col.align || 'left',
            }"
          >
            {{ col.label }}
          </th>
          <th v-if="showActions">Actions</th>
        </tr>
      </thead>

      <!-- Body -->
      <tbody>
        <tr
          v-for="(row, rowIndex) in data"
          :key="rowIndex"
          :class="rowIndex % 2 === 0 ? 'row-even' : 'row-odd'"
        >
          <td
            v-for="(col, colIndex) in headers"
            :key="colIndex"
            :style="{
              width: col.width ? col.width + 'px' : 'auto',
              textAlign: col.align || 'left',
            }"
          >
            {{ row[col.field] }}
          </td>
          <td v-if="showActions" class="actions-cell">
            <span class="action-btn edit" @click="$emit('edit', row)">✏️</span>
            <span class="action-btn delete" @click="$emit('delete', row)">
              🗑️
            </span>
          </td>
        </tr>

        <!-- Empty State -->
        <tr v-if="data.length === 0">
          <td
            :colspan="headers.length + (showActions ? 1 : 0)"
            style="text-align: center; padding: 10px"
          >
            No data available
          </td>
        </tr>
      </tbody>

      <!-- Footer -->
      <tfoot v-if="footer && footerData">
        <tr>
          <td v-for="(col, index) in headers" :key="index">
            {{ footerData[col.field] || '' }}
          </td>
          <td v-if="showActions"></td>
        </tr>
      </tfoot>
    </table>

    <!-- Add Button -->
    <button v-if="showActions" class="add-btn" @click="$emit('add')">
      ➕ Add New
    </button>
  </div>
</template>

<script>
  export default {
    name: 'TableComponent',
    props: {
      headers: { type: Array, required: true },
      data: { type: Array, required: true },
      footer: { type: Boolean, default: false },
      footerData: { type: Object, default: () => ({}) },
      border: { type: Boolean, default: true },
      borderRadius: { type: Number, default: 8 },
      evenRowColor: { type: String, default: '#f9f9f9' },
      oddRowColor: { type: String, default: '#ffffff' },
      hoverColor: { type: String, default: '#e0e0e0' },
      showActions: { type: Boolean, default: false },
    },
    computed: {
      tableStyles() {
        return {
          '--even-color': this.evenRowColor,
          '--odd-color': this.oddRowColor,
          '--hover-color': this.hoverColor,
          '--border-radius': this.borderRadius + 'px',
        }
      },
    },
  }
</script>

<style scoped>
  .table-wrapper {
    width: 100%;
    overflow-x: auto;
  }

  .custom-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    font-family: Arial, sans-serif;
    overflow: hidden;
    border-radius: var(--border-radius);
  }

  .table-border th,
  .table-border td {
    border: 1px solid #ddd;
    padding: 8px;
  }

  thead th {
    background-color: #0bbb98;
    color: white;
    font-weight: bold;
    padding: 10px;
  }

  tfoot td {
    background-color: #f5f5f5;
    font-weight: bold;
    padding: 10px;
  }

  .row-even {
    background-color: var(--even-color);
  }
  .row-odd {
    background-color: var(--odd-color);
  }

  tbody tr:hover {
    background-color: var(--hover-color) !important;
  }

  .actions-cell {
    text-align: center;
    white-space: nowrap;
    width: 80px;
  }

  .action-btn {
    cursor: pointer;
    margin: 0 6px;
    font-size: 18px;
  }

  .action-btn.edit {
    color: #007bff;
  }
  .action-btn.delete {
    color: #dc3545;
  }

  .add-btn {
    margin-top: 12px;
    background: #0bbb98;
    color: white;
    padding: 8px 14px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
  }

  .add-btn:hover {
    background: #099b7a;
  }
</style>
