import { createRouter, createWebHistory } from 'vue-router'
import AnimalExplorer from '../components/FirstGradeComponents/AnimalExplorer.vue'
import EmojiGame from '../components/FirstGradeComponents/EmojiGame.vue'
import FirstGradeGame from '../components/FirstGradeComponents/FirstGradeGame.vue'
import TriangleCountGame from '../components/FirstGradeComponents/TriangleCountGame.vue'
import Dashboard from '../components/views/Dashboard.vue'
import Login from '../components/views/login.vue'
import Profile from '../components/views/Profile.vue'
import ShapePuzzle from '../components/views/ShapePuzzle.vue'
import Signup from '../components/views/Signup.vue'

// Fifth Grade Games
import CoordinateBattleship from '../components/FifthGradeComponents/CoordinateBattleship.vue'
import DecimalQuest from '../components/FifthGradeComponents/DecimalQuest.vue'
import EquationBalance from '../components/FifthGradeComponents/EquationBalance.vue'
import FractionPizza from '../components/FifthGradeComponents/FractionPizza.vue'
import FractionsPuzzle from '../components/FifthGradeComponents/FractionsPuzzle.vue'
import GeometryExplorer from '../components/FifthGradeComponents/GeometryExplorer.vue'
import LogicMaze from '../components/FifthGradeComponents/LogicMaze.vue'
import MathPlayground from '../components/FifthGradeComponents/MathPlayground.vue'
import MathRiddles from '../components/FifthGradeComponents/MathRiddles.vue'
import MultiplicationRace from '../components/FifthGradeComponents/MultiplicationRace.vue'
import PrimeNumbersGame from '../components/FifthGradeComponents/PrimeNumbersGame.vue'
import ReadingExplorer from '../components/FifthGradeComponents/ReadingExplorer.vue'
import ScienceExplorer from '../components/FifthGradeComponents/ScienceExplorer.vue'
import DinosaurExplorer from '../components/FirstGradeComponents/DinosaurExplorer.vue'

const routes = [
  { path: '/', name: 'login2', component: Login },
  { path: '/login', name: 'login', component: Login },
  { path: '/signup', name: 'signup', component: Signup },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
    meta: { requiresAuth: true },
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    meta: { requiresAuth: true },
  },
  {
    path: '/kindergarten',
    name: 'Kindergarten',
    component: EmojiGame,
    meta: { requiresAuth: true },
  },
  {
    path: '/first-grade',
    name: 'FirstGrade',
    component: FirstGradeGame,
    meta: { requiresAuth: true },
  },
  {
    path: '/triangles',
    name: 'Triangles',
    component: TriangleCountGame,
    meta: { requiresAuth: true },
  },
  {
    path: '/shapepuzzle',
    name: 'ShapePuzzle',
    component: ShapePuzzle,
    meta: { requiresAuth: true },
  },
  {
    path: '/animalExplorer',
    name: 'AnimalExplorer',
    component: AnimalExplorer,
    meta: { requiresAuth: true },
  },

  // Fifth Grade Games
  {
    path: '/fifth-grade/game1',
    name: 'PrimeNumbersGame',
    component: PrimeNumbersGame,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game2',
    name: 'FractionsPuzzle',
    component: FractionsPuzzle,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game3',
    name: 'GeometryExplorer',
    component: GeometryExplorer,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game4',
    name: 'MathRiddles',
    component: MathRiddles,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game5',
    name: 'LogicMaze',
    component: LogicMaze,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game6',
    name: 'EquationBalance',
    component: EquationBalance,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game7',
    name: 'FractionPizza',
    component: FractionPizza,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game8',
    name: 'MultiplicationRace',
    component: MultiplicationRace,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game9',
    name: 'DecimalQuest',
    component: DecimalQuest,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game11',
    name: 'CoordinateBattleship',
    component: CoordinateBattleship,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game13',
    name: 'DinosaurExplorer',
    component: DinosaurExplorer,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game14',
    name: 'ScienceExplorer',
    component: ScienceExplorer,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game15',
    name: 'ReadingExplorer',
    component: ReadingExplorer,
    meta: { requiresAuth: true },
  },
  {
    path: '/fifth-grade/game16',
    name: 'MathPlayground',
    component: MathPlayground,
    meta: { requiresAuth: true },
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
})

// Global navigation guard
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('authToken')

  if (to.meta.requiresAuth && !token) {
    next({ path: '/' })
  } else {
    next()
  }
})

export default router
